.nav{
    position: fixed;
    top:0;
    display: flex;
    padding: 20px;
    height: 30px;
    width: 100%;
    justify-content: space-between;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_logo{
    width: 80px;
    position: fixed;
    left: 14px;
    top: 1px;
    height: 80px;
    object-fit: contain;
}
.nav_black{
    background-color: black;
}
.nav_icon{
    width: 40px;
    position: fixed;
    top: 23px;
    right: 20px;
    object-fit: contain;
}